import React from 'react';
import { CardStyle } from './Card.styled';

export const Card = ({ card }) => {
  const { name, img, id, material, thickness, length, width, works } = card;

  return (
    <CardStyle>
      <img src={img} alt={name} width="264" height="260" />
      <p>: {id}</p>
      <p>: {material}</p>
      <p>: {thickness}</p>
      <p>: {length}</p>
      <p>: {width}</p>
      <p> : {works}</p>
    </CardStyle>
  );
};
